.PostShare{
    display: flex;
    gap:1rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;

}
.PostShare>img{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
}
.PostShare>div{
    display: flex;
    flex-direction: column;
    gap:1rem;
    width: 90%;
}
.PostShare>div>input{
    background: var(--inputColor);
    border-radius: none;
    padding: 10px;
    border: none;
    font-size: 17px;
    outline: none;
}
.PostOptions{
    display: flex;
   justify-content: space-around;

}
.option{
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;

    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    border-radius: 10px;
}
.option:hover{
    cursor: pointer;
}
.ps-button{
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 12px;
}
.previewImage{
    position: relative;

}
.previewImage>svg{
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer;
}
.previewImage>img{
    width: 100%;
    max-height:20rem;
    object-fit:cover;
    border-radius: 0.5rem;
}
