.ProfileCard{
    border-radius: 1.5rem;
    display:flex;
    flex-direction: column;
    position:relative;
    gap:1rem;
  overflow-x: clip;
  background:var(--cardColor);
}
.ProfileImages{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
}
.ProfileImages>img:nth-of-type(1){
    width:100%;
    
}
.ProfileImages>img:nth-of-type(2){
    width:6rem;
    border-radius: 50%;
    position: absolute;
    bottom:-3rem;
    box-shadow: var(--profileShadow);
}
.ProfileName{
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    gap:10px
}
.ProfileName>span:nth-of-type(1){
    font-weight: bold;
}
.followStatus{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:0.75rem;
}
.followStatus>hr{
    width: 85%;
    bordr:1px solid var(--hrColor);
}
.followStatus>div{
    display: flex;
    width: 80%;
    justify-content: space-around;
    align-items: center;
    gap:1rem;

}
.follow{
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;
    gap:0.4rem;

}
.follow>span:nth-of-type(2){
    color:var(--gray);
    font-size: 13px;
    font-size: 13px;
}
.vl{
    height: 150px;
    border-left: 2px solid var(--hrColor);
}
.ProfileCard>span{
    font-weight: bold;
    color:orange;
    align-self: center;
    margin-bottom: 1rem;
    cursor:pointer;
}